.loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
  
  .loader {
    border: 6px solid rgba(0, 0, 0, 0.1);
    border-top: 6px solid #007bff; /* Blue loader color */
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  